
'use client';

import { useEffect, useState } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { collection, onSnapshot, query, orderBy } from 'firebase/firestore';
import { db } from '@/lib/firebase';
import { PawPrintIcon, PlusCircleIcon, TagIcon, CalendarIcon } from '@heroicons/react/24/solid';

// Función para formatear Timestamps de Firestore a una fecha legible
function formatFecha(timestamp) {
    if (!timestamp) return 'Fecha no disponible';
    const date = timestamp.toDate();
    return new Intl.DateTimeFormat('es-ES', { dateStyle: 'long' }).format(date);
}

// Componente visual mejorado para cada mascota
function MascotaCard({ mascota }) {
    return (
        <div className="bg-white/70 backdrop-blur-sm rounded-xl shadow-lg overflow-hidden transition-transform duration-300 hover:scale-[1.02] hover:shadow-xl">
            <div className={`p-5 border-l-8 ${mascota.enAdopcion ? 'border-amber-500' : 'border-violet-500'}`}>
                <div className="flex items-center justify-between">
                    <h3 className="text-2xl font-bold text-gray-800">{mascota.nombre}</h3>
                    {mascota.enAdopcion && (
                        <div className="flex items-center gap-2 bg-amber-100 text-amber-800 text-xs font-semibold px-3 py-1 rounded-full">
                            <TagIcon className="h-4 w-4" />
                            <span>En Adopción</span>
                        </div>
                    )}
                </div>
                <p className="text-md text-gray-600 font-medium capitalize">{mascota.especie} - {mascota.raza || 'Sin raza'}</p>
                <div className="flex items-center gap-2 mt-3 text-sm text-gray-500">
                    <CalendarIcon className="h-5 w-5 text-violet-400" />
                    <span>Nacimiento: {formatFecha(mascota.fechaNacimiento)}</span>
                </div>
            </div>
             <Link href={`/mascotas/${mascota.id}/carnet`} className="block bg-gray-50 hover:bg-gray-100 text-center text-violet-700 font-semibold py-3 px-4 transition-colors duration-200">
                Ver Carnet Sanitario
            </Link>
        </div>
    );
}

export default function MascotasPage() {
    const { user, loading: authLoading } = useAuth();
    const router = useRouter();
    const [mascotas, setMascotas] = useState([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        if (authLoading) return;
        if (!user) {
            router.replace('/login');
            return;
        }

        const mascotasRef = collection(db, 'users', user.uid, 'mascotas');
        const q = query(mascotasRef, orderBy('fechaRegistro', 'desc'));

        const unsubscribe = onSnapshot(q, (snapshot) => {
            const mascotasData = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
            setMascotas(mascotasData);
            setLoading(false);
        }, (error) => {
            console.error("Error al obtener las mascotas:", error);
            toast.error("No se pudieron cargar las mascotas.");
            setLoading(false);
        });

        return () => unsubscribe();
    }, [user, authLoading, router]);

    if (loading || authLoading) {
        return (
            <div className="flex justify-center items-center h-screen bg-violet-100">
                <div className="text-center">
                    <PawPrintIcon className="animate-spin h-12 w-12 text-violet-600 mx-auto" />
                    <p className="text-violet-800 font-semibold mt-2">Cargando tus mascotas...</p>
                </div>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-violet-200 font-sans">
            <main className="container mx-auto px-4 py-8">
                <div className="flex flex-col sm:flex-row justify-between items-center mb-8 gap-4">
                    <h1 className="text-4xl font-extrabold text-gray-800 tracking-tight">Mis Mascotas</h1>
                    <Link href="/mascotas/nueva" className="inline-flex items-center gap-2 bg-violet-600 hover:bg-violet-700 text-white font-bold py-2 px-4 rounded-full shadow-lg transition-all duration-300 transform hover:scale-105">
                        <PlusCircleIcon className="h-6 w-6" />
                        <span>Agregar Mascota</span>
                    </Link>
                </div>

                {mascotas.length === 0 ? (
                    <div className="text-center bg-white/60 backdrop-blur-sm p-12 rounded-2xl shadow-lg mt-10">
                        <PawPrintIcon className="mx-auto h-16 w-16 text-violet-400" />
                        <h2 className="mt-6 text-2xl font-bold text-gray-800">Aún no has registrado ninguna mascota.</h2>
                        <p className="text-gray-600 mt-2 mb-8 max-w-prose mx-auto">¡Tu compañero fiel merece estar aquí! Añádelo para llevar un control de su salud y solicitar turnos de forma sencilla.</p>
                        <Link href="/mascotas/nueva" className="inline-block bg-green-500 hover:bg-green-600 text-white font-bold py-3 px-8 rounded-full text-lg shadow-xl transition-transform duration-200 hover:scale-105">
                            Registrar mi Primera Mascota
                        </Link>
                    </div>
                ) : (
                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                        {mascotas.map(mascota => (
                            <MascotaCard key={mascota.id} mascota={mascota} />
                        ))}
                    </div>
                )}
            </main>
        </div>
    );
}
